import ArrowForwardRoundedIcon from "@mui/icons-material/ArrowForwardRounded";
import { Avatar, Box, Container, Stack, Typography, useTheme } from "@mui/material";
import { useCallback } from "react";
import type { HeroContent } from "../types";
import { GhostButton, Pill, PrimaryButton } from "@shared/ui";
import certifiedBadgeSrc from "../../../../images/badges/certified-badge.png";
import { smoothScrollToElement } from "@shared/utils/smoothScroll";

type HeroSectionProps = {
  hero: HeroContent;
};

const HeroSection = ({ hero }: HeroSectionProps) => {
  const theme = useTheme();
  const handlePrimaryCtaClick = useCallback(() => {
    const targetId = hero.ctaPrimary.scrollToId ?? hero.ctaPrimary.to;
    if (!targetId || typeof document === "undefined") {
      return;
    }

    if (targetId.startsWith("/")) {
      return;
    }

    const sanitizedId = targetId.startsWith("#") ? targetId.slice(1) : targetId;
    const element = document.getElementById(sanitizedId);
    if (element) {
      smoothScrollToElement(element, { duration: 900, offset: 16 });
    }
  }, [hero.ctaPrimary.scrollToId, hero.ctaPrimary.to]);

  const handleSecondaryCtaClick = useCallback(() => {
    const targetId = hero.ctaSecondary.scrollToId ?? hero.ctaSecondary.to;
    if (!targetId || typeof document === "undefined") {
      return;
    }

    if (targetId.startsWith("/")) {
      return;
    }

    const sanitizedId = targetId.startsWith("#") ? targetId.slice(1) : targetId;
    const element = document.getElementById(sanitizedId);
    if (element) {
      smoothScrollToElement(element, { duration: 900, offset: 16 });
    }
  }, [hero.ctaSecondary.scrollToId, hero.ctaSecondary.to]);

  return (
    <Box
      component="section"
      sx={(theme) => ({
        py: { xs: 6, md: 9 },
        background:
          theme.palette.mode === "light"
            ? "radial-gradient(circle at 20% 15%, rgba(145, 167, 255, 0.12), transparent 58%), radial-gradient(circle at 80% 20%, rgba(34, 84, 255, 0.1), transparent 52%)"
            : "radial-gradient(circle at 18% 15%, rgba(73, 109, 193, 0.18), transparent 60%), radial-gradient(circle at 78% 18%, rgba(48, 86, 176, 0.18), transparent 55%)",
      })}
    >
      <Container>
        <Stack
          direction={{ xs: "column", md: "row" }}
          spacing={{ xs: 4, md: 3.5 }}
          sx={{
            alignItems: { xs: "center", md: "stretch" },
            px: { xs: 0, md: 2 },
          }}
        >
          <Stack
            spacing={2.5}
            sx={{
              flexBasis: { md: "30%" },
              maxWidth: { md: "30%" },
              flexShrink: 0,
              alignItems: { xs: "center", md: "flex-start" },
              textAlign: { xs: "center", md: "left" },
              width: "100%",
            }}
          >
            <Box
              sx={{
                position: "relative",
                width: "100%",
                maxWidth: { xs: 220, sm: 280, md: 320 },
              }}
            >
              <Avatar
                src={hero.portrait.src}
                alt={hero.portrait.alt}
                variant="rounded"
                imgProps={{
                  loading: "eager",
                  decoding: "async",
                }}
                sx={{
                  width: "100%",
                  height: "auto",
                  borderRadius: (theme) => theme.tokens.radius.lg,
                  border: (theme) => `1px solid ${theme.tokens.colors.border}`,
                  boxShadow: (theme) =>
                    theme.palette.mode === "light"
                      ? "0 18px 40px rgba(17, 36, 83, 0.18)"
                      : "0 20px 50px rgba(0, 0, 0, 0.55)",
                  backgroundColor: theme.palette.mode === "light" 
                    ? "rgba(34, 84, 255, 0.06)" 
                    : "rgba(145, 167, 255, 0.08)",
                }}
              />
              <Box
                component="img"
                src={certifiedBadgeSrc}
                alt="Certified badge"
                loading="lazy"
                sx={{
                  position: "absolute",
                  top: { xs: -18, md: -24 },
                  left: { xs: -18, md: -24 },
                  width: { xs: 120, md: 150 },
                  height: "auto",
                  pointerEvents: "none",
                  userSelect: "none",
                }}
              />
            </Box>
          </Stack>
          <Stack
            spacing={{ xs: 2.25, md: 2.75 }}
            sx={{
              flexBasis: { md: "70%" },
              maxWidth: { md: "70%" },
              flexGrow: 1,
              justifyContent: "center",
              alignItems: { xs: "center", md: "flex-start" },
              textAlign: { xs: "center", md: "left" },
            }}
          >
            <Stack spacing={{ xs: 1.4, md: 1.8 }} sx={{ width: "100%" }}>
              <Stack
                direction={{ xs: "column", sm: "row" }}
                spacing={{ xs: 1, sm: 1.5 }}
                alignItems={{ xs: "center", sm: "flex-start" }}
              >
                <Pill
                  label={hero.greeting}
                  size="small"
                  sx={{
                    backgroundColor:
                      theme.palette.mode === "light"
                        ? "rgba(31, 111, 235, 0.12)"
                        : "rgba(98, 132, 218, 0.22)",
                    color:
                      theme.palette.mode === "light"
                        ? theme.palette.brand.secondary
                        : "rgba(221, 230, 255, 0.92)",
                    fontWeight: 600,
                    letterSpacing: 0.3,
                  }}
                />
              </Stack>
              <Typography
                variant="display"
                sx={{
                  lineHeight: 1.05,
                  letterSpacing: "-0.015em",
                  fontWeight: 700,
                }}
              >
                {hero.name}
              </Typography>
              <Typography
                variant="h2"
                sx={{
                  color: theme.palette.text.secondary,
                  fontWeight: 500,
                  letterSpacing: { xs: "0.01em", md: "0.005em" },
                  maxWidth: { md: "85%" },
                  fontSize: { xs: "1.5rem", sm: "1.75rem", md: "2rem" },
                }}
              >
                {hero.title}
              </Typography>
            </Stack>

            <Stack spacing={2}>
              <Typography
                variant="subtitle1"
                sx={{
                  color: theme.palette.text.secondary,
                  fontSize: { xs: "1.05rem", md: "1.12rem" },
                  lineHeight: { xs: 1.65, md: 1.7 },
                  fontWeight: 500,
                }}
              >
                {hero.subtitle.split(" | ")[0]}
              </Typography>
              {hero.subtitle.includes(" | ") && (
                <Typography
                  variant="body1"
                  sx={{
                    color: theme.palette.text.secondary,
                    fontSize: { xs: "0.95rem", md: "1rem" },
                    lineHeight: { xs: 1.6, md: 1.65 },
                    opacity: 0.9,
                  }}
                >
                  {hero.subtitle.split(" | ").slice(1).join(" | ")}
                </Typography>
              )}
              {/* Key Value Pills */}
              <Stack
                direction="row"
                spacing={1}
                flexWrap="wrap"
                sx={{
                  pt: 0.5,
                  gap: 1,
                  justifyContent: { xs: "center", md: "flex-start" },
                }}
              >
                <Pill
                  label="40+ Million Users"
                  size="small"
                  sx={{
                    backgroundColor:
                      theme.palette.mode === "light"
                        ? "rgba(31, 111, 235, 0.1)"
                        : "rgba(98, 132, 218, 0.15)",
                    color:
                      theme.palette.mode === "light"
                        ? theme.palette.brand.secondary
                        : "rgba(221, 230, 255, 0.9)",
                    fontWeight: 600,
                    fontSize: { xs: "0.75rem", md: "0.8125rem" },
                  }}
                />
                <Pill
                  label="B2B & B2C"
                  size="small"
                  sx={{
                    backgroundColor:
                      theme.palette.mode === "light"
                        ? "rgba(31, 111, 235, 0.1)"
                        : "rgba(98, 132, 218, 0.15)",
                    color:
                      theme.palette.mode === "light"
                        ? theme.palette.brand.secondary
                        : "rgba(221, 230, 255, 0.9)",
                    fontWeight: 600,
                    fontSize: { xs: "0.75rem", md: "0.8125rem" },
                  }}
                />
                <Pill
                  label="Data-Driven"
                  size="small"
                  sx={{
                    backgroundColor:
                      theme.palette.mode === "light"
                        ? "rgba(31, 111, 235, 0.1)"
                        : "rgba(98, 132, 218, 0.15)",
                    color:
                      theme.palette.mode === "light"
                        ? theme.palette.brand.secondary
                        : "rgba(221, 230, 255, 0.9)",
                    fontWeight: 600,
                    fontSize: { xs: "0.75rem", md: "0.8125rem" },
                  }}
                />
                <Pill
                  label="AI-Familiar"
                  size="small"
                  sx={{
                    backgroundColor:
                      theme.palette.mode === "light"
                        ? "rgba(31, 111, 235, 0.1)"
                        : "rgba(98, 132, 218, 0.15)",
                    color:
                      theme.palette.mode === "light"
                        ? theme.palette.brand.secondary
                        : "rgba(221, 230, 255, 0.9)",
                    fontWeight: 600,
                    fontSize: { xs: "0.75rem", md: "0.8125rem" },
                  }}
                />
                <Pill
                  label="Technical & Management"
                  size="small"
                  sx={{
                    backgroundColor:
                      theme.palette.mode === "light"
                        ? "rgba(31, 111, 235, 0.1)"
                        : "rgba(98, 132, 218, 0.15)",
                    color:
                      theme.palette.mode === "light"
                        ? theme.palette.brand.secondary
                        : "rgba(221, 230, 255, 0.9)",
                    fontWeight: 600,
                    fontSize: { xs: "0.75rem", md: "0.8125rem" },
                  }}
                />
              </Stack>
            </Stack>

            <Stack
              spacing={{ xs: 1.5, sm: 2 }}
              direction={{ xs: "column", sm: "row" }}
              sx={{ pt: { xs: 0.5, md: 0.75 } }}
            >
              <PrimaryButton
                endIcon={<ArrowForwardRoundedIcon />}
                size="large"
                sx={{ 
                  minWidth: { sm: 220 },
                  minHeight: 44,
                  py: { xs: 1.25, md: 1.5 },
                }}
                onClick={handlePrimaryCtaClick}
              >
                {hero.ctaPrimary.label}
              </PrimaryButton>
              <GhostButton
                onClick={handleSecondaryCtaClick}
                size="large"
                sx={{
                  borderRadius: 999,
                  px: 3,
                  borderWidth: 1.5,
                  minHeight: 44,
                  py: { xs: 1.25, md: 1.5 },
                }}
              >
                {hero.ctaSecondary.label}
              </GhostButton>
            </Stack>
          </Stack>
        </Stack>
      </Container>
    </Box>
  );
};

export default HeroSection;


